// Project image component
function ProjectImage({ project }: { project: typeof projects[0] }) {
  const videoRef = useRef<HTMLVideoElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)
  
  // Restart video when it becomes visible
  useEffect(() => {
    if (!project.video || !containerRef.current) return
    
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && videoRef.current) {
            videoRef.current.currentTime = 0
            videoRef.current.play()
          }
        })
      },
      { threshold: 0.5 }
    )
    
    observer.observe(containerRef.current)
    return () => observer.disconnect()
  }, [project.video])
  
  return (
    <div ref={containerRef} className="relative w-full h-full rounded-[16px] lg:rounded-[24px] overflow-hidden">
      <Image src={project.image} alt={project.title} fill className="object-cover" />
      <div className="absolute inset-0 bg-black/20 pointer-events-none" />
      {project.video && (
        <video
          ref={videoRef}
          src={project.video}
          autoPlay
          loop
          muted
          playsInline
          className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[70%] rounded-lg shadow-2xl z-10"
        />
      )}
    </div>
  )
}

