  const workHeaderOpacity = useTransform(scrollYProgress, [0.35, 0.39, 0.82, 0.86], [0, 1, 1, 0])
  const playHeaderOpacity = useTransform(scrollYProgress, [0.84, 0.88], [0, 1])
  
